/**
 * Proposal component style
 *
 */

.Proposal__card {
    display: flex;
    flex-direction: column;
    width: 80%;
    height: auto;
    justify-content: center;
    align-items: center;
    margin: 3.125rem auto;
    text-align: center;
    img {
        width: 180px;
        height: 180px;
        border-radius: 100%;
        border: 4px solid $pink;
        @include respond-to('large') {
            width: 18.75rem;
            height: 18.75rem;
        }
    }
    &::before {
        display: none;
    }
    p {
        margin-bottom: 0;
    }
}

.Proposal__infos {
    display: flex;
    height: 10.625rem;
    flex-direction: column;
    justify-content: space-between;
}

.Proposal__infos__card__number {
    margin: 1.875rem 0 1.25rem 0;
}

.Proposal__infos__job {
    &::before,
    &::after {
        content: "-";
        margin: 0 0.625rem;
    }
}

.Proposal__choice {
    margin-top: 30px;
}

