/*
* Loading page style
*
*/

.Loading {
    width: 100vw;
    height: 100vh;
    background-color: $black;
}

.Loading__container {
    height: 90vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Loading__heart__container {
    width: 3.125rem;
    /*50px*/
    height: 5.625rem;
    /*90px*/
    position: relative;
    margin-left: 12.5rem;
    /*200px*/
}

.Loading__heart__1 {
    background: url('../img/heart.svg');
    background-size: cover;
    position: absolute;
    bottom: 0;
    left: 25px;
    width: 40px;
    height: 35px;
    opacity: 0;
    animation: heartbounce__1 2.5s infinite linear 0.3s;
}

.Loading__heart__2 {
    background: url('../img/heart.svg');
    background-size: cover;
    position: absolute;
    bottom: 45px;
    left: 70px;
    width: 20px;
    height: 17.5px;
    animation: heartbounce__2 2.5s infinite linear;
}



@keyframes heartbounce__1 {
  0%   {bottom: 0px ;  transform: translateX(0px);  opacity: 1;}
  10%  {bottom: 15px;  transform: translateX(-5px);}
  20%  {bottom: 30px;  transform: translateX(5px); }
  30%  {bottom: 45px;  transform: translateX(5px); }
  40%  {bottom: 60px;  transform: translateX(-5px);}
  50%  {bottom: 75px;  transform: translateX(-5px);}
  60%  {bottom: 90px;  transform: translateX(5px); }
  70%  {bottom: 105px; transform: translateX(5px); }
  80%  {bottom: 120x;  transform: translateX(-5px);}
  90%  {bottom: 135px; transform: translateX(-5px);}
  100% {bottom: 150px; transform: translateX(5px);  opacity: 0;}
}

@keyframes heartbounce__2 {
  0%   {bottom: 45px ;  transform: translateX(0px);  opacity: 1;}
  10%  {bottom: 60px;  transform: translateX(-3px);}
  20%  {bottom: 75px;  transform: translateX(3px); }
  30%  {bottom: 90px;  transform: translateX(3px); }
  40%  {bottom: 105px;  transform: translateX(-3px);}
  50%  {bottom: 120px;  transform: translateX(-3px);}
  60%  {bottom: 135px;  transform: translateX(3px); }
  70%  {bottom: 150px; transform: translateX(3px); }
  80%  {bottom: 175x;  transform: translateX(-3px);}
  90%  {bottom: 190px; transform: translateX(-3px);}
  100% {bottom: 205px; transform: translateX(3px);  opacity: 0;}
}

.Loading__logo {
    background: url('../img/starloversloading.svg');
    background-size: cover;
    align-items: center;
    width: 800px;
    height: 150px;
    @media only screen and (orientation: portrait) and (min-height: 48rem) {
        width: 600px;
        height: 115px;
        background-size: 100%;
        background-repeat: no-repeat;
    }
}

.Loading__infos__text {
    font-family: "Open Sans", sans-serif;
    position: absolute;
    font-weight: bold;
    font-size: 0.88rem;
    text-align: center;
    text-transform: uppercase;
    color: $pink;
    margin-left: -205px;
    @media only screen and (orientation: portrait) and (min-height: 48rem) {
        width: 600px;
        height: 115px;
        background-size: 100%;
        background-repeat: no-repeat;
        margin-left: -360px;
    }
}